$size-large: 1440px;
$size-mobile-width: 720px;

$color-key: #B31F37;


* {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

html {padding: 0;}
body {
	margin: 0; padding: 0;
	font-family: 'Noto Sans', sans-serif;
	font-size: 14px; color: #111;
	line-height: 1.428;
	-webkit-text-size-adjust: none;
}
a {color: $color-key;}
main {
	min-width: 320px;
	&.mini-width {min-width: 200px;}
}



// popup mode
html.popup-mode {
	//overflow: hidden;
	//&, body {height: 100%;}
	main {
		display: none;
	}
}